.hero {
  position: relative;
}
.hero h3 {
  font-weight: 100;
  letter-spacing: 2px;
}
.hero h1 {
  font-size: 60px;
  font-weight: bold;
}

.hero h1 span {
  color: #ff014f;
}
.hero h2 {
  font-size: 50px;
  font-weight: bold;
  color: #ff014f;
}
.hero h2 span {
  color: black;
}
.hero p {
  color: #878e99;
  font-size: 18px;
  line-height: 30px;
  margin-top: 30px;
}
.cv-btn {
  margin-top: 5%;
  padding: 18px 25px;
  border-radius: 6px;
  transition: 0.3s all ease;
  cursor: pointer;
  height: fit-content;

  background: #ff014f;
  color: white;
}

.cv-btn {
  box-shadow: 4px 4px 8px #cbcbcb, -4px -4px 8px #ffffff;
}
.cv-btn:hover {
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  color: #ff014f;
}
.hero_btn {
  margin-top: 5%;
}

.hero_btn h4 {
  font-weight: 500;
  letter-spacing: 2px;
  color: #43474b;
  font-size: 15px;
  margin: 30px 0 30px 0;
}
.hero_btn button {
  margin-right: 20px;
  margin-bottom: 5%;
}
.hero_btn i {
  font-size: 30px;
}

.hero_btn button img {
  width: 30px;
  height: 30px;
}
.hero .left {
  margin-right: 10%;
}
.hero .right {
  width: 40%;
  position: relative;
}
.hero .right_img {
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 4px 4px 8px #cbcbcb, -4px -4px 8px #ffffff;
  border-radius: 10px;
  height: 580px;
  width: 510px;
  margin-top: 20%;
}
.hero .right_img img {
  position: absolute;
  top: 0;
  right: 10%;
  width: 510px;
}
@media (max-width: 768px) {
  .hero .right,
  .hero .left {
    width: 100%;
    margin-left: 20px;
  }

  .hero .f_flex {
    flex-direction: column;
    flex-direction: column-reverse;
  }
  .hero .right_img {
    width: 100%;
    height: 74vh;
  }

  .hero .right_img img {
    position: absolute;
    top: 0;
    right: 1%;
    width: 500px;
    height: 91vh;
  }
}
